import React from "react";
import "../style.css";
import "bootstrap/dist/css/bootstrap.min.css";
import { Formik, Form, Field, ErrorMessage } from "formik";

class ArrayPlay extends React.Component {
  // const [firstArray, setFirstArray] = React.useState("fill");
  // const [wasSubmitted, setWasSubmitted] = React.useState(false);
  // const [answer, setAnswer] = React.useState("");
  state = {
    formData: {
      fName: "1, 23, 19, 8",
    },
    wasSubmitted: false,
    answer: [],
  };

  handleSubmit = (values) => {
    console.log(values.fName);
    let res = Array.from(values.fName.split(","), Number);
    res.sort(function (a, b) {
      return a - b;
    });
    console.log(res);
    this.setState((prevState) => {
      return {
        prevState,
        wasSubmitted: true,
        answer: res,
      };
    });
  };

  render() {
    return (
      <div className="services section-bg">
        <div className="container">
          <div className="section-title">
            <h2>Array Play</h2>
            <p>
              Enter a string of numbers to be sorted (Seperated by a comma or
              blank space)
            </p>
          </div>
          <div className="row">
            <Formik
              enableReinitialize={true}
              initialValues={this.state.formData}
              onSubmit={this.handleSubmit}
            >
              <Form>
                <div className="form-group">
                  <div className="form-wrapper">
                    <Field type="text" name="fName" />
                    <ErrorMessage name="fName" component="div" />
                  </div>
                  <button type="submit" className="btn btn-secondary">
                    Submit
                  </button>
                </div>
              </Form>
            </Formik>
            {this.state.wasSubmitted ? <div>{this.state.answer}</div> : ""}
          </div>
        </div>
      </div>

      // </div>
      // </div>
    );
  }
}

export default ArrayPlay;
