import React from "react";
import EggCountdown from './EggCountdown';


class Chameleons extends React.Component{
    state={
        chams: [{name: "Gretchen",
                type: "Ambanja",
                dueDate: '2021-06-20T01:02:03',
                id: 1,
                img: "https://i.imgur.com/2RQt8hq.jpg" },
                {name: "Frances",
                type: "Ambanja",
                dueDate: '2021-06-18T01:02:03',
                id: 2,
                img: "https://i.imgur.com/SK01QlN.jpg" }],
        mappedChams: [],
    }
    componentDidMount() {
       console.log("chams component mounted");
       let newChams = this.state.chams.map(this.mapChams);
       this.setState((prevState) => {
        return {
          prevState,
          mappedChams: newChams,
        }
       })
    }

    mapChams = (cham) => {
        return(
            <React.Fragment key={cham.id}>
                <EggCountdown 
                cham={cham}/>
            </React.Fragment>
        )
    }

    render(){
        return( <div className="services section-bg">
             
    <div className="container">
      
      <div className="section-title">
       
        <h2>Egg Tracker</h2>
      
        <p>Here are the chameleons we're expecting eggs from soon.</p>
      </div>
      <div className="row">
        {this.state.mappedChams}
        </div>
        </div> 
        </div>     
        );
    }
}

export default Chameleons;